* {
  box-sizing: border-box;
}

:root {
  --main-red-color: #e06262;
  --main-black-color: #484848;
}

html {
  font-size: 10px;
}

body {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;

  background-color: #2d2d2d;
  height: 100vh;
}

body,
html {
  padding: 0;
  margin: 0;
  font-family: "Ranchers", cursive;
}
